// 全局变量
@primary-color: #222d52;
@green-color: #222D52;
@secondary-color: #fff;

// 全局样式
.layout {
    min-height: 100vh;

    .ant-layout-header {
        padding: 0 50px;
    }

    .ant-layout-footer {
        text-align: center;
    }
}

// 响应式设计
@media (max-width: 768px) {
    .layout {
        .ant-layout-header {
            padding: 0 20px;
        }
    }
}

