@import "../../styles/global.less"; // 引入全局变量

.header-component {
    box-sizing: border-box;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 1000;
    background: #fff;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
    height: 64px;

    .logo {
        font-family: Cambria, Cochin, Georgia, Times, "Times New Roman", serif;
        width: 200px;
    }

    .header-component-nav {
        box-sizing: border-box;
        width: 100%;

        .nav-menu {
            width: 100%;
            height: 100%;
            background-color: #fff;

            .nav-item {
                color: black;
                font-weight: bold;
                font-size: 20px;
            }
        }
    }
}

@primary-color: #222d52;@link-color: #222d52;@success-color: #52c41a;@warning-color: #faad14;@error-color: #f5222d;@font-size-base: 14px;@heading-color: rgba(0, 0, 0, 0.85);@text-color: rgba(0, 0, 0, 0.65);@text-color-secondary: rgba(0, 0, 0, 0.45);@border-radius-base: 4px;